body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #1e1b2a, #4b367c);
  color: white;
  height: 100vh;
  overflow: hidden;
}

.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  margin: 0;
  border-radius: 0;
  box-shadow: none;
  background: #1c1730;
  padding: 8px;
}

.chatbot-avatar {
  margin-top: env(safe-area-inset-top, 16px);
}
